.search-chat {
  font-family: inherit;
}

.search-chat__label {
  display: block;
  position: relative;
}

.search-chat__img {
  cursor: text;
  position: absolute;
  top: 50%;
  left: 35%;
  transform: translateY(-50%);
}

.search-chat__input {
  box-sizing: border-box;
  width: 100%;
  color: #1e1e1e;
  background-color: #efefef;
  border-width: 1px;
  border-color: #0000;
  border-radius: 5px;
  outline: none;
  padding: 9px;
  font-size: 10px;
  font-weight: 500;
  line-height: 10px;
}

.search-chat__input::placeholder {
  color: #999;
  text-align: center;
  font-size: 10px;
  font-weight: 500;
  line-height: 10px;
}

